/*
Call the var function to retrieve variables from sass map variables

ex:
var( $vars, navpage, hero, border-color );

*/
ul.navigation__items {
  list-style: none;
  margin: 0;
  padding: 0;
}
ul.navigation__items a {
  display: flex;
  height: 4rem;
  align-items: center;
  text-decoration: none;
  font-weight: bold;
}
ul.navigation__items .children a {
  font-weight: normal;
}
ul.navigation__items.ancestors > .navigation__item:nth-of-type(1) a {
  padding-left: 1rem;
}
ul.navigation__items.ancestors > .navigation__item:nth-of-type(1) .children a {
  padding-left: 3rem;
}
ul.navigation__items.ancestors > .navigation__item:nth-of-type(2) a {
  padding-left: 2rem;
}
ul.navigation__items.ancestors > .navigation__item:nth-of-type(2) .children a {
  padding-left: 4rem;
}
ul.navigation__items.ancestors > .navigation__item:nth-of-type(3) a {
  padding-left: 3rem;
}
ul.navigation__items.ancestors > .navigation__item:nth-of-type(3) .children a {
  padding-left: 5rem;
}
ul.navigation__items.ancestors > .navigation__item:nth-of-type(4) a {
  padding-left: 4rem;
}
ul.navigation__items.ancestors > .navigation__item:nth-of-type(4) .children a {
  padding-left: 6rem;
}
ul.navigation__items .umbrella a {
  background-color: #1a4480;
  color: #fff;
}
ul.navigation__items .active > a {
  border-left: 0.5rem solid #d83933;
  color: #d83933;
}
ul.navigation__items .sibling > a {
  font-weight: normal;
  margin-left: 0.5rem;
}

.usagov-mobile-menu {
  width: 100%;
  max-width: 30rem;
  padding: 0;
  overflow: scroll;
  z-index: 2000000003;
}

.usagov-mobile-menu-top {
  padding: 1rem;
  background-color: #f3f3f3;
}

.usagov-mobile-menu-controls {
  display: flex;
  justify-content: space-between;
}

.usagov-mobile-menu-controls > * {
  display: flex;
  align-items: center;
  background: transparent;
  border: 0;
  color: black;
  font-weight: bold;
  font-size: larger;
  text-decoration: none;
  margin: unset;
  width: auto;
  height: auto;
}

.usagov-mobile-menu-controls a img {
  padding: 0.16rem;
  margin: 0.1rem;
  border: 3px solid black;
  border-radius: 100%;
  height: 2.16rem;
}

.usagov-mobile-menu-controls button img {
  height: 2.5rem;
  width: auto;
}

.usagov-mobile-menu .usa-nav__secondary-links {
  margin: 1rem;
  display: block;
}

.usa-header--extended button.usa-menu-btn {
  background-color: transparent;
  color: #112f4e;
  background-image: url(/themes/custom/usagov/images/Button_header_open_mobile-menu.svg);
  background-size: 2.8rem;
  background-position: 1px 0px;
  width: 3rem;
  padding: 0;
  padding-top: 2rem;
  height: auto;
  font-size: 1rem;
  font-weight: normal;
  cursor: pointer;
}

#block-languageswitcher {
  position: relative;
}

@media (min-width: 64em) {
  .usagov-mobile-menu {
    display: none;
  }
}
@media (max-width: 63.99em) {
  .usa-nav.is-visible {
    display: none;
  }
  .usagov-mobile-menu.is-visible {
    display: flex;
  }
  .usa-header--extended div.language-switcher-language-url ul.links {
    position: absolute;
    display: block;
    width: auto;
    right: 60px;
    top: 4px;
  }
